<template>
  <div id="wrap" class="vw" :class="{ d_none: isnone }">
    <div id="wnt">
      <input type="text" id="nt" ref="rnt" @select="sel" />
    </div>
    <div
      id="code"
      ref="rco"
      onselectstart="return false"
      @click.capture="codeIn"
    >
      <p v-for="(hz, i) in ahz" :key="i" @click="$emit('delAz', { fl })">
        {{ hz }}
      </p>
      <p v-for="(a, i) in az" :key="i" @click="iA">{{ a }}</p>

      <p>,</p>
      <p>.</p>
      <p class="suc" @click="calc">﹦</p>
      <p
        @click="
          fl = !fl;
          az = az.map(function (x) {
            return fl ? x.toUpperCase() : x.toLowerCase();
          });
        "
      >
        ⇑
      </p>
      <p>{</p>
      <p>}</p>
      <p>|</p>
      <p>\</p>
      <p>https://</p>
      <p>&nbsp;</p>
      <p ref="rim" class="suc" @click="kbdin">{{ lan }}</p>
      <p class="suc" @click="$emit('ac')">AC</p>
      <p class="suc" @click="$emit('back', iA)">Back</p>
      <p class="suc" @click="bing">Enter</p>
    </div>
  </div>
</template>
<script>
export default {
  props: ["idkww", "calc", "codeIn"],
  data() {
    return {
      fl: !1,
      az: `qwertyuiop[]/'"asdfghjkl;:<>?zxcvbnm`.split(""),
      ahz: "`1234567890-=~!@#$%^&*()_+".split(""),
      lan: "EN",
      isnone: false,
    };
  },
  methods: {
    kbdin() {
      this.fl = !this.fl;
      this.iA();
      if (this.fl) {
        this.$refs.rim.style.background = "#bff";
        this.lan = "CH";
        // this.$refs.rim[this.S_] = 'CH';
        // this.$refs.rco.addEventListener('click', this.iA);
        // for (var i = 0; i < 26; i++) {
        //     this.$refs.rco.children[i].addEventListener('click', this.iB)
        // }
      } else {
        this.$refs.rim.style.background = "#dff";
        this.lan = "EN";
        // this.$refs.rim[this.S_] = 'EN';
        // this.$refs.rco.removeEventListener('click', this.iA, false);
        this.ahz = "`1234567890-=~!@#$%^&*()_+".split("");
        // for (var i = 0; i < 26; i++) {
        //     this.$refs.rco.children[i][this.S_] = a[i];
        //     //     this.$refs.rco.children[i].removeEventListener('click', this.iB, false)

        // }
      }
    },
    iA() {
      if (this.fl) {
        var s = this.idkww.match(/\w+/),
          ar,
          l;

        if (!s) {
          return;
        }
        s = s[0];
        ar = hz[s] ? hz[s].split("") : this.ahz;
        l = ar.length;
        if (l < 26) {
          ar = ar.concat(this.ahz.slice(l));
        }
        this.ahz = ar;
      }
    },
    bing() {
      var url = this.idkww.match(/^http|^www|^m\./)
        ? ""
        : "https://cn.bing.com/search?q=";
      window.open(url + this.idkww);
    },
  },
  mounted() {
    if (this.$root.pphh) {
      this.$refs.rnt.setAttribute("readonly", "readonly");
    } else {
      this.$parent.$refs.rkw.focus();
    }
    // console.log(this);
  },
};
</script>