
  <template>
  <div>
    <ol>
      <li>按按钮【help】按钮会显示本帮助文档</li>
      <li>虚拟键盘输入数学计算公式可以计算结果，输入【1+3】，再点击 #calc。</li>
      <li>
        搜索框如果不输入关键字，点击链接就进入网页主页，输入关键字点击连接则进入连接的关键字搜索页面。
      </li>
      <li>
        输入关键字【red，green，blue，purple，gray，pink，yellow，gold，orange】等英文单词再按搜索【color】链接设置背景颜色，网页默认背景色为“#102456”，输入后可以更改为默认颜色，支持16进制颜色值和RGB颜色函数
      </li>
      <li>
        输入搜索串eg【dict http://m.dict/cn/】再点击 $DIY$
        会添加自定义搜索链接，相当于可以自定义一个搜索引擎,例子dict已设置为搜索链接了。输入【dict】会删除自定义搜索
      </li>
      <li>虚拟键盘输入网址，必须要加上https或http前缀，再按虚拟enter</li>
      <li>输入关键字后虚拟enter或实体enter按键进行bing搜索</li>

      <li>虚拟键盘最后的【AC】是清空搜索框的文字内容</li>
      <li>虚拟键盘最后的【back】是清除搜索框的文字内容最后一个内容</li>

      <li>导航game连接是九宫格拼图，点击空白区域的上下左右区域切换</li>
      <li>按钮【hide】会清除弹出的显示结果</li>
      <li>按钮【video，music，img】点击会显示随机显示</li>

      <li>4输入字符串【qwert3】再按搜索链接【repeat】会重复输出</li>
      <li>输入字符串【qqqwwee】再按搜索链接【uniq】会去重字符串</li>
      <li>输入字符串【abcd】再按搜索链接【reverse】会倒序字符串</li>
      <li>
        输入收尾两个数字【1 9】再按seq搜索键会产生1至9序列；输入三个数字【1 30
        5】会产生以5为步进的序列
      </li>
      <li>
        输入任意字符再按【ASCII】会转换字符的ASCII码，也可ASCII码变成对应字符
      </li>
      <li>
        输入正则表达式和文本可以查找全部匹配，eg【[d]+
        as12ao43-ee33】以上输入再按【Match】搜索会提取所有连续数字，默认全局匹配，忽略大小写
      </li>
      <li>
        输入要替换的字符，和新字符和文本可以全部替换子串，eg【abc`xyz`123abcabc】输入再按【replace】会替换所有abc为xyz，输入参数有分隔符，第一参数支持正则
      </li>
      <li>
        输入字符再点【length】输出字符长，输入以空格分隔的文本再点【tac】会逆序
      </li>
      <li>
        输入以空格分隔的文本再点【col】会把文本变成列，支持正则【正则`文本】格式
      </li>
      <li>一个数字和文本再按【for】可以序列文本，多个数字，序列数字前加`</li>
      <li>
        输入关键字再按【tee】会把关键字保存在键盘上面的保存和快捷输入上，电脑上复制选择会把关键字推到搜索框里,需要删除记录需要输入关键字"RL()"再按第一个【#calc】会清除记录
      </li>
      <li>输入连续文本或空格隔开文本，再按【count】会统计各项总数</li>
