<template>
  <div class="home">
    <form id="input">
      <input
        spellcheck="false"
        type="text"
        id="keyword"
        placeholder="💲"
        v-model="idkww"
        ref="rkw"
        @blur="isnone = false"
        @click="togRo"
      />
    </form>

    <div id="line" class="vw w70" @click="baidu"></div>
    <div class="vw w70 ifrbd" v-if="lb" v-html="idb"></div>

    <div id="box" class="vw w70" v-if="!lb">
      <a url="" @click="calc"># calc</a>

      <links :idkww="idkww" ref="rlinks" :ls="ls" />

      <a href="#" @click="fdiy">$DIY$</a>
      <a href="#" @click="f6">ASCII</a>

      <a href="#" @click="f2">Color</a>
      <a href="#" @click="f11">Col</a>
      <a href="#" @click="f17">Count</a>

      <a
        href="#"
        @click="
          (lf = !lf), (idkww = lf ? idkww.toUpperCase() : idkww.toLowerCase())
        "
        >azAZ</a
      >

      <a href="#" @click="idt = getS().split('<br>').join(' ')">Each</a>

      <a href="#" @click="idv = getS()">For</a>
      <a href="#" @click="f21">Filter</a>

      <a href="#" @click="idv = idkww.length">Length</a>

      <a href="#" @click="f8">Match</a>
      <a href="#" @click="f20">Map</a>

      <a href="#" @click="f22">Reduce</a>
      <a href="#" @click="idkww = idkww.split('').reverse().join(' ')"
        >Reverse</a
      >
      <a href="#" @click="f3">Repeat</a>
      <a href="#" @click="f9">Sed</a>

      <a href="#" @click="fsort">Sort</a>
      <a href="#" @click="f7">Seq</a>
      <a href="#" @click="f24">sra</a>

      <a href="#" @click="idkww = idkww.split(/\s+/).reverse().join(' ')"
        >Tac</a
      >
      <a href="#" @click="f16">Tee</a>
      <a href="#" @click="f25">Tr</a>
      <a href="#" @click="f4">Uniq</a>
      <a href="#" @click="f19">0x0b0</a>
      <a href="#" @click="f23">utf&Un</a>

      <a href="#" @click="idt = idkww ? eval(idkww) : ''">#calc</a>
    </div>

    <div id="bu" class="vw w70" v-if="!lb">
      <template
        v-for="(v, i) in [
          'dirs',
          'site',
          'pc',
          'hide',
          'pic',
          'tool',
          'prog',
          'help',
        ]"
        :key="i"
      >
        <input hidden type="radio" :id="v" :value="v" v-model="radioOne" />
        <button>
          <label :for="v">{{ v }}</label>
        </button>
      </template>
    </div>

    <!-- <hr class="vw" width="70%" style="border: 1px #000 solid; margin: 0 auto" /> -->

    <div id="view" class="vw">
      <div v-html="idv" v-text="idt"></div>

      <dirs v-if="radioOne === 'dirs'" />
      <helps v-if="radioOne === 'help'" />
      <site v-if="radioOne === 'site'" />
      <prog v-if="radioOne === 'prog'" />
    </div>

    <div id="mus" class="vw"></div>

    <vkbd
      :idkww="idkww"
      :calc="calc"
      :codeIn="codeIn"
      @ac="idkww = ''"
      @back="
        idkww = idkww.slice(0, -1);
        $event();
      "
      @delAz="if ($event.fl) idkww = idkww.replace(/\w/g, '');"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Links from "@/components/Links.vue";
import Vkbd from "@/components/Vkbd.vue";
import Helps from "@/components/Helps.vue";
import Site from "@/components/Site.vue";
import Prog from "@/components/Prog.vue";
import Dirs from "@/components/Dirs.vue";

export default {
  name: "Das",
  components: {
    Links,
    Vkbd,
    Helps,
    Site,
    Prog,
    Dirs,
  },
  data: function () {
    return {
      _s: _s,
      _g: _g,
      idkww: "",
      lf: 0,
      he: !1,
      tog: !0,
      idv: "",
      idt: "",
      isph: this.$root.pphh,
      bg: "",
      S_: "\x69\x6e\x6e\x65\x72\x54\x65\x78\x74",
      radioOne: "",
      idb: "",
      lb: 0,
      imgs: [
        "/th?id=OHR.SalisburyCathedral_ZH-CN6366350896",
        "/th?id=OHR.PalouseSpring_ZH-CN6803103328",
        "/th?id=OHR.RubySunset_ZH-CN5544596519",
        "/th?id=OHR.FalklandRockhoppers_ZH-CN5370686595",
        "/th?id=OHR.MegellanicCloud_ZH-CN5132305226",
        "/th?id=OHR.KauriTree_ZH-CN3695568740",
        "/th?id=OHR.SouthAmericanTapir_ZH-CN6151058361",
      ],
      ls: _g("ls") || "",
    };
  },
  mounted() {
    this.cookie();
    var bj = window.getComputedStyle(document.body).backgroundColor;

    if (bj === "rgba(0, 0, 0, 0)") {
      bj = "#102456";
      document.body.style.background = bj;
      document.getElementById(
        "nav"
      ).style.background = `linear-gradient(${bj},#fff)`;
    }
    document.addEventListener("keydown", (e) => {
      var e = event || window.event;
      var keyCode = e.keyCode || e.witch;
      if (e.keyCode == 13) {
        var bing = this.idkww.match(/^http|^www|^m\./)
          ? ""
          : "https://cn.bing.com/search?q=";
        window.open(bing + this.idkww);
      }
    });
    this.eval = eval.bind(eval);
    // document.body.style.background = "#102456";
  },
  methods: {
    togRo() {
      if (this.isph) {
        this.$refs.rkw.removeAttribute("readonly");
        this.isnone = true;
      }
    },
    baidu() {
      this.lb = !this.lb;
      this.idb = this.lb
        ? `<iframe width=100%  src="https://m.baidu.com">`
        : "";
    },
    calc() {
      var v = this.idkww;
      this.idkww = v ? (v.match(/^_\w{1,3}_$/) ? eval(v + "()") : eval(v)) : "";
    },
    fdiy() {
      var re = new RegExp(this.idkww + ".*?;");
      if (this.idkww.match(/\s+/)) {
        _s("ls", this.idkww + ";" + _g("ls"));
        this.ls = _g("ls");
      } else {
        _s("ls", _g("ls").replace(re, ""));
        this.ls = _g("ls");
      }
      // this.$refs.rlinks.fjoin();
    },

    f25() {
      var k = this.idkww,
        ar = k.split(/\s+/),
        o = ar[0],
        n = ar[1],
        s = ar[2];
      this.idkww = _trr_(o, n, s);
    },
    f24() {
      var k = this.idkww,
        ar = k.split(/\s+/),
        s = ar[0],
        r = new RegExp(ar[1], "g"),
        a = ar.slice(2);
      this.idkww = _sra_(s, r, a);
    },
    f23() {
      var s = this.idkww,
        z,
        x;
      if (s.match(/^%/)) {
        z = decodeURIComponent(s);
        x = z.charCodeAt(0).toString(16);
        this.idkww = "\\u" + x;
      } else {
        z = s.match(/^\d/)
          ? String.fromCharCode(s)
          : String.fromCharCode("0x" + s.slice(2) - 0);
        console.log(z);
        x = encodeURIComponent(z);
        this.idkww = x;
      }
    },
    f22() {
      this.gj(".reduce(");
    },
    f21() {
      this.gj(".filter(").join(" ");
    },
    f20() {
      this.gj(".map(").join(" ");
    },
    f19() {
      var s = this.idkww,
        a = s.split(/\s+/);
      if (a.length <= 2) {
        this.idv = (0 | a[0]).toString(0 | a[1]);
      } else if (s.match(/[a-z]/)) {
        this.idkww = a
          .map((a) => a.replace(/\\u/, "0x"))
          .map((a) => 0 | a)
          .join(" ");
      } else {
        this.idkww = a
          .map((a) => (0 | a).toString(16))
          .map((a) => "\\u" + a)
          .join(" ");
      }
    },
    f18() {},
    f17() {
      var s = this.idkww,
        ar = s.match(/\s+/) ? s.split(/\s+/) : s.split(""),
        c = JSON.stringify(
          ar.reduce(function (r, i) {
            r[i]++ || (r[i] = 1), r;
            return r;
          }, {})
        );
      this.idv = c;
    },
    f16() {},
    fsort() {
      this.idkww = this.idkww
        .split(/\s+/.test(this.idkww) ? /\s+/ : "")
        .sort()
        .join(" ");
    },
    f15() {},
    f14() {},
    f13() {},
    f12() {},
    f11() {
      var txta = this.idkww.split("`"),
        alen = txta.length,
        reg = alen === 1 ? new RegExp("\\s+", "g") : new RegExp(txta[0], "g");
      this.idv = txta[alen - 1].replace(reg, "<br>");
    },
    f10() {},
    f9() {
      var txta = this.idkww.split("`"),
        reg = new RegExp(txta[0], "g");
      this.idt = txta[2].replace(reg, txta[1]);
    },
    f8() {
      var txt = this.idkww,
        x = txt.split("`"),
        reg = new RegExp(x[0], "g", "i");
      this.idt = x[1].match(reg).join(" ");
    },
    f7() {
      var strr = this.idkww.split(/\s+/),
        start = parseInt(strr[0]),
        end = parseInt(strr[1]),
        step = strr.length == 3 ? parseInt(strr[2]) : 1,
        sum = "";
      for (var i = start; i <= end; i = i + step) {
        sum = sum + (i + " ");
      }
      this.idkww = sum.slice(0, -1);
    },
    f6() {
      var str = this.idkww;
      this.idkww = str.match(" ")
        ? String.fromCharCode.apply("", str.split(/ +/))
        : str
            .split("")
            .map(function (a) {
              return a.charCodeAt();
            })
            .join(" ");
    },
    f5() {},
    f4() {
      var str = this.idkww;
      if (str.indexOf(" ") < 0) {
        str = this.idkww.split("").join(" ");
      }
      this.idkww = str
        .split(/\s+/)
        .reduce(function (r, i) {
          return r.indexOf(i) === -1 ? r.push(i) : r, r;
        }, [])
        .join(" ");
    },
    f3() {
      var s = this.idkww,
        r,
        n,
        t,
        f = "`";
      if (s.match(/\d+$/)) {
        n = RegExp["$&"] - 0;
        t = RegExp["$`"];
        r = s.match(f) ? RegExp["$`"] : t;
      }
      this.idkww = new Array(n + 1 || 1).join(r + " ");
    },
    f2() {
      document.cookie =
        "bg=" + this.idkww + ";expires=Thu, 01 Jan 2029 00:00:00 UTC";
      // this.bg = document.cookie.match(/bg=[^;]+/)[0].slice(3);

      this.cookie();
    },

    f1() {},
    cookie() {
      var bj = document.cookie.match(/bg=[^;]+/)
        ? document.cookie.match(/bg=[^;]+/)[0].slice(3)
        : "#102456";
      document.body.style.background = bj;
      document.getElementById(
        "nav"
      ).style.background = `linear-gradient(${bj},#fff)`;
    },
    gj(g) {
      var s = this.idkww,
        a = s.split("`"),
        f = a[0],
        b = a[1],
        c = b.split(/\s+/),
        j = b.match(/\D\s/)
          ? c
          : c.map(function (x) {
              return 0 | x;
            }),
        r = JSON.stringify(j);
      this.idkww = eval(r + g + f + ")");
    },

    seqq() {
      var seq = "",
        re = /_[a-zA-Z]{1,2}_(\(.*?\))?/g,
        a1,
        a2,
        a0,
        n,
        i;
      if (!this.idkww.match(re)) {
        seq = this.idkww;
      } else {
        a1 = this.idkww.match(re);
        a0 = a1.map(function (x) {
          return eval(x);
        });
        a2 = this.idkww.replace(re, "_#").split("_#");
        n = a1.length;
        for (i = 0; i < n; i++) {
          seq += a2[i] + a0[i];
        }
        seq += a2[n];
      }
      return seq;
    },

    getS() {
      var s = "",
        ar,
        l,
        seq = this.seqq(),
        sep;
      (function () {
        l = seq.match(/\d+/g);
        if (l == null) ar = ["", 0, ""];
        if (l && l.length == 1) {
          sep = seq.split(/\d+/);
          ar = [sep[0], Number(l[0]), sep[1]];
        }
        if (l && l.length > 1 && seq.indexOf("`") > -1) {
          sep = seq.split(/`\d+/);
          ar = [sep[0], Number(seq.match(/`\d+/)[0].slice(1)), sep[1]];
        }
        return ar;
      })();
      for (var j = 1; j <= ar[1]; j++) {
        s = s + ar[0] + j + ar[2] + "<br>";
      }
      return s;
    },

    codeIn(e) {
      var p = this.$refs.rkw.selectionStart;
      this.idkww = e.target[this.S_].match(/﹦|⇑|EN|CH|AC|Back|Enter/)
        ? this.idkww
        : this.idkww.slice(0, p) + e.target[this.S_] + this.idkww.slice(p);

      // console.log(p);
      if (this.isph) this.$refs.rkw.setAttribute("readonly", "readonly");

      this.$refs.rkw.focus();
      this.$refs.rkw.selectionStart = !e.target[this.S_].match(/ht/)
        ? p + 1
        : p + 8;
    },

    // iB() {
    //     this.idkww = this.idkww.replace(/\w/g, '')
    // },

    sel() {
      this.idkww = this.idkww + getSelection();
    },
  },
  watch: {
    radioOne(v) {
      if (v === "pic") {
        this.idv =
          "<img src='https://cn.bing.com" +
          this.imgs[~~(Math.random() * 7)] +
          "_1920x1080.jpg'>";
        this.idmus = "";
      } else {
        this.idv = "";
      }
    },
  },
};
</script>