<template>
  <div id="nav" class="vw">
    <div class="brand" style="margin-left: 5vw">
      <router-link to="/das" tag="button">DAS</router-link>
    </div>
    <div style="margin-right: 5vw">
      <router-link to="/game">Game</router-link>|
      <a @click="fmus" href="#">music</a>
    </div>
  </div>
  <router-view />
</template>


<script>
export default {
  data() {
    return {
      once: 0,
      pphh: /android|iphone|ipad|nokia|blackberry/i.test(navigator.userAgent),
    };
  },
  beforeUpdate() {
    console.log("");
  },
  methods: {
    fmus() {
      this.once = !this.once;
      console.log(this.once);
      if (this.once) {
        var musics = [
          "BIGBANGHaruHaru",
          "RainOverMe",
          "TimeMachine",
          "卫兰大哥",
          "吴雨霏LetMeGo",
          "坏天使",
          "张靓颖漫步人生路",
          "王力宏伯牙绝弦",
        ];
        function ra() {
          return (
            "https://ddaasss.github.io/" +
            musics[~~(Math.random() * 8)] +
            ".mp3"
          );
        }
        var audiod = document.createElement("audio");
        audiod.src = ra();
        audiod.controls = "controls";
        document.getElementById("mus").appendChild(audiod);
        audiod.play();
        audiod.loop = false;
        audiod.addEventListener(
          "ended",
          function () {
            audiod.src = ra();
            audiod.play();
          },
          false
        );
      } else {
        document.getElementById("mus").innerHTML = "";
      }
    },
  },
};
</script>

